import React from 'react';
import '../App.css';
import ballons from '../assets/ballons.png';
import hbText from '../assets/hb-text.png';
import star from '../assets/star.png';
import arrow from '../assets/arrow.png';
import {NavLink} from 'react-router-dom';

function Main() {
  return (
    <div className="main">
      <div className="ballon">
        <img src={ballons} alt=""/>
      </div>
      <div className="hb-text">
        <img src={hbText} alt=""/>
      </div>
      <div className="ballon">
        <img src={ballons} alt=""/>
      </div>
      <div className="stars">
        <div className="star"></div>
          <img src={star} alt="" className="star1"/>
          <img src={star} alt="" className="star2"/>
          <img src={star} alt="" className="star3"/>
          <img src={star} alt="" className="star4"/>
          <img src={star} alt="" className="star5"/>
          <img src={star} alt="" className="star6"/>
          <img src={star} alt="" className="star7"/>
          <img src={star} alt="" className="star8"/>
          <NavLink  to="/greeting"><img src={arrow} alt="" className="arrow"/></NavLink>
      </div>
    </div>
  );
}

export default Main;