import React, { useState } from 'react';
import '../App.css';
import greeting from '../assets/bg-greeting.png';
import arrow from '../assets/arrow.png';
import { NavLink } from 'react-router-dom';
import photo1 from '../assets/1.jpg';
import photo2 from '../assets/2.jpg';
import photo3 from '../assets/3.jpg';
import photo4 from '../assets/4.jpg';
import photo5 from '../assets/5.jpg';
import photo6 from '../assets/6.jpg';
import photo7 from '../assets/7.jpg';
import photo8 from '../assets/8.jpg';
import photo9 from '../assets/9.jpg';
import photo10 from '../assets/10.jpg';
import photo11 from '../assets/11.jpg';
import photo12 from '../assets/12.jpg';
import photo13 from '../assets/13.jpg';
import photo14 from '../assets/14.jpg';
import photo15 from '../assets/15.jpg';

function Greeting() {
  const [photo, setPhoto] = useState(1)
  return (
    <div className="greeting">
      <div className="photos">
        <img src={arrow} alt="" className="left" onClick={() => setPhoto(photo > 1 ? photo - 1 : 8)} />
        <div className={`photo_box ${(photo === 1) && "active"}`}>
          <img src={photo2} alt="" className="photo" />
          <img src={photo3} alt="" className="photo" />
        </div>
        <div className={`photo_box ${(photo === 2) && "active"}`}>
          <img src={photo7} alt="" className="photo" />
          <img src={photo11} alt="" className="photo" />
        </div>
        <div className={`photo_box2 ${(photo === 3) && "active"}`}>
          <img src={photo1} alt="" className="photo" />
        </div>
        <div className={`photo_box ${(photo === 4) && "active"}`}>
          <img src={photo5} alt="" className="photo" />
          <img src={photo12} alt="" className="photo" />
        </div>
        <div className={`photo_box ${(photo === 5) && "active"}`}>
          <img src={photo6} alt="" className="photo" />
          <img src={photo13} alt="" className="photo" />
        </div>
        <div className={`photo_box ${(photo === 6) && "active"}`}>
          <img src={photo9} alt="" className="photo" />
          <img src={photo10} alt="" className="photo" />
        </div>
        <div className={`photo_box ${(photo === 7) && "active"}`}>
          <img src={photo14} alt="" className="photo" />
          <img src={photo15} alt="" className="photo" />
        </div>
        <div className={`photo_box2 ${(photo === 8) && "active"}`}>
          <img src={photo4} alt="" className="photo" />
        </div>
        <img src={arrow} alt="" className="right" onClick={() => setPhoto(photo < 8 ? photo + 1: 1)} />
      </div>
      <div className="greeting__text">
        <h1>Віточка, з Днем народження! В першу чергу бажаю міцного здоров'я! Бажаю тобі тільки приємних моментів у житті і багато сюрпризів!)Нехай кожен день буде сповнений добром та щастям!) Щирого кохання!) Побільше посмішок і радості!
Багаааато подорожей і яскравих емоцій! І здійснення всіх найзаповітніших мрій!! Чекаю у Кракові!</h1>
        <NavLink to="/cake"><img src={arrow} alt="" className="arrow" /></NavLink>
      </div>
    </div>
  );
}

export default Greeting;