import React from 'react';
import '../App.css';
import cake from '../assets/cake.svg';
import cake2 from '../assets/cake1.png';
import { useState } from 'react';

function Cake() {

  const [iscake, setcake] = useState(false)
  return (
    <div className="cake">
      <div>
        <img src={iscake ? cake : cake2} alt="" onClick={()=>{setcake(true)}}/>
      </div>
    </div>
  );
}

export default Cake;